import { TransactionData } from './../../../core/models/userTransactionHistory';
import { HttpSuccesFailureResponse } from './../../../core/http_wrapper/http_wrapper_response';
import { HttpUserAuthUtilsService } from './../../../core/http_wrapper/http-userauth-utils.service';
import { Component, OnInit } from '@angular/core';
import { LocalStorageService } from 'core/service/local-storage.service';
import { GlobalSubscriptionService } from 'app/globalSubcription.service';

declare var $: any;

@Component({
  selector: 'app-transactionhistory',
  templateUrl: './transactionhistory.component.html',
  styleUrls: ['./transactionhistory.component.css']
})

export class TransactionhistoryComponent implements OnInit,HttpSuccesFailureResponse {
 
 userTransactionDataList:TransactionData[]=[];
 totalTransactions:number=0;
  currencyCode: any;
  ngAfterViewInit() {
    $('#clickpdf').click(function() {
      $('#paymentDetail').DataTable( {
        "ordering":false,
        dom: 'B',
        "bPaginate": false,
        buttons: [
          {
            extend:'pdf',
            footer:true,
            title:'Transaction History',
            customize: function ( doc ) {
              var rowcount=doc.content[1].table.body.length;
          for (var i = 0; i < rowcount; i++) { 
                doc.content[1].table.body[i][4].alignment = 'right';
                doc.content[1].table.body[i][4].fontSize = '9';
                doc.content[1].table.body[i][3].fontSize = '9';
                doc.content[1].table.body[i][2].fontSize = '9';
                doc.content[1].table.body[i][1].fontSize = '9';
                doc.content[1].table.body[i][0].fontSize = '9';

            };
            }

          }
        ]
        
    } );
    
      $('.buttons-pdf').click().hide();
      
    
} );
    $(document).ready(function() {
        window.alert = function(){};
    });

  }

  constructor(private http_user:HttpUserAuthUtilsService,private globalSubscriptionService: GlobalSubscriptionService
    ) { }

  ngOnInit() {
    this.currencyCode = LocalStorageService.getCurrencyCode();
    this.http_user.getUserTransactions(this)
    this.globalSubscriptionService.changeCountryState.subscribe((value) => { 
        this.ngOnInit();
      });
  }
 onSuccess(type: any, responsedata: any) { 
   this.userTransactionDataList=responsedata.data;
   this.totalTransactions=this.userTransactionDataList.length;
  }
  onFailure(type: any, response: string) {

  }

}



