  getDuration(selectedPackage):number {
    let value = -1;
    switch (selectedPackage.validityPeriod) {
      case PackageTypes.month:
        value = 30 * 24 * 3600 * 1000;
        break;
      case PackageTypes.days:
        value = 24 * 3600 * 1000;
        break;
      case PackageTypes.week:
        value = 7 * 24 * 3600 * 1000;
        break;
      case PackageTypes.year:
        value = 365 * 24 * 3600 * 1000;
    }
    return value;
  }


