  setSectionMessages() {
 if (this.userDetails.data.userPackageDetailDtoList !== undefined && this.userDetails.data.userPackageDetailDtoList.length > 0) {
   let userDropinPackageCount = 0, userGeneralPackageCount = 0;
   this.userDetails.data.userPackageDetailDtoList.forEach(element => {
     if (element.packageType === PackageEnums.dropin) {
        userDropinPackageCount++;
     }if (element.packageType === PackageEnums.general) {
      userGeneralPackageCount++;
     }
     });

     if (userDropinPackageCount > 0 && userGeneralPackageCount === 0) {
  // have refunded drop-in as current plan
  this.sectionOneMessage = MembershipMessagesCase2.section1;
   this.sectionTwoMessage = MembershipMessagesCase2.section2;
   this.sectionThreeMessage = MembershipMessagesCase2.section3;
     } else if (userGeneralPackageCount > 0 && this.sessionId !== undefined && !this.userDetails.data.buyMembership ) {
       // Has active plan and user not able to join a class and comes from join class
       this.sectionOneMessage = MembershipMessagesCase4.section1;
       this.sectionTwoMessage = MembershipMessagesCase4.section2;
       this.sectionThreeMessage = MembershipMessagesCase4.section3;
     } else if (userGeneralPackageCount > 0 && this.sessionId !== undefined && this.userDetails.data.buyMembership ) {
      // Has active plan and user not able to join a class and comes from join class
      this.sectionOneMessage = MembershipMessagesCase2.section1;
      this.sectionTwoMessage = MembershipMessagesCase4.section2;
      this.sectionThreeMessage = MembershipMessagesCase4.section3;
    } else if (userGeneralPackageCount > 0) {
      // Has get set/pro and user is on membership page
      this.sectionOneMessage = MembershipMessagesCase3.section1;
      this.sectionTwoMessage = MembershipMessagesCase3.section2;
      this.sectionThreeMessage = MembershipMessagesCase3.section3;
     }


 } else {
   this.sectionOneMessage = MembershipMessagesCase1.section1;
   this.sectionTwoMessage = MembershipMessagesCase1.section2;
   this.sectionThreeMessage = MembershipMessagesCase1.section3;
   // case no classes
 }

  }
