import { ModalModule } from 'angular2-modal';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TooltipModule } from '../../core/components/tooltip/tooltip';
import { AboutusComponent } from './aboutus/aboutus.component';
import { TransactionhistoryComponent } from './transactionhistory/transactionhistory.component';
import { PromoCodeModule } from './promoCode/promocode.module';
import { MemberShipExtensionModule } from './membershipExtension/membershipextension.module';
import { ChangeCountryComponent } from './change-country/change-country.component';

@NgModule({
  imports: [
      routing,
      CoreModule,
      CommonModule,
      TooltipModule,
      SlickModule.forRoot(),
      ModalModule.forRoot(),
      BootstrapModalModule,
      FormsModule,
      ReactiveFormsModule,
      PromoCodeModule,
      MemberShipExtensionModule  ],
  declarations: [
      UseroptionsComponent,
      MembershipComponent,
      UsersnotiComponent,
      AboutusComponent,
      TransactionhistoryComponent,
      ChangeCountryComponent,

  ],
  entryComponents: [
  ],
  schemas:[CUSTOM_ELEMENTS_SCHEMA],
  exports: [
  ]
})
export class UserOptionsModule { }



