import { Component, OnInit } from '@angular/core';
import { Constants } from 'core/constants';
import { PakagesData } from 'core/models/packagesListResponse';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { LocalStorageService } from 'core/service/local-storage.service';
import { GlobalSubscriptionService } from 'app/globalSubcription.service';
import { UserSession } from 'core/sessions';

@Component({
  selector: 'app-aboutus',
  templateUrl: './aboutus.component.html',
  styleUrls: ['./aboutus.component.css']
})
export class AboutusComponent implements OnInit {

  public noOfCredits:any[]=[];
  public costOfClasses:any[]=[];
  currencyCode: any;
  public gymName=UserSession.getDynamicElements(Constants.GymName)

  constructor(private http_uau:HttpUserAuthUtilsService,private globalSubscriptionService:GlobalSubscriptionService) { }

  ngOnInit() { 
    this.currencyCode = LocalStorageService.getCurrencyCode();

    this.http_uau.getMembershipPackages('',this,true);
    

    this.globalSubscriptionService.changeCountryState.subscribe((value) => { 
      this.currencyCode = LocalStorageService.getCurrencyCode();
      this.http_uau.getMembershipPackages('',this,false);

    
    });
  }

  onSuccess(type: string, responsedata: any) { 
    if (type == Constants.getPackageListApi) {
      this.noOfCredits=[];
      this.costOfClasses=[];
      const filteredList: PakagesData[] = responsedata.data;
      // this.noOfCredits[0] = 1;
      // this.costOfClasses[0]= 'based on gym';
     // this.packages = responsedata.data;
      for(let i=0;i<filteredList.length;i++) {
        this.noOfCredits[i] = filteredList[i].credits;
        this.costOfClasses[i]=filteredList[i].cost;
      } //for
    }
  }
  onFailure() { }
}



