import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpUserAuthUtilsService } from "../../../core/http_wrapper/http-userauth-utils.service";
import { LocalStorageService } from '../../../core/service/local-storage.service';
import { HttpSuccesFailureResponse } from "../../../core/http_wrapper/http_wrapper_response";
import { Constants } from '../../../core/constants';
import { MixpanelService } from '../../../core/mixpanelService.service';
@Component({
  selector: 'app-usersnoti',
  templateUrl: './usersnoti.component.html',
  styleUrls: ['./usersnoti.component.css']
})
export class UsersnotiComponent implements OnInit, HttpSuccesFailureResponse {

  public timer: Observable<any>;
  public subscription: Subscription;
  public showHide:boolean=false;
  public isActive: boolean = false;
  @ViewChild("notificationCheck") private notificationCheckbox;

  constructor(private _httpUserAuth: HttpUserAuthUtilsService, private mixpanel: MixpanelService) { }

  ngOnInit() {
        this.timer = Observable.timer(500); // 5000 millisecond means 5 seconds
    this.subscription = this.timer.subscribe(() => {
           this.showHide=true;
    });

    this._httpUserAuth.getUserProfile(LocalStorageService.getUserId(), this, true);
  }

  onCheckboxChange() {
    this.isActive = !this.isActive;
    this._httpUserAuth.updateUserNotificationPreference(LocalStorageService.getUserId(),this,true,this.isActive);
  }

  onSuccess(type: any, responsedata: any) {

   if (type === Constants.FindUserApi) {
    this.mixpanel.updateUserInfoToMixpanel(LocalStorageService.getUser())
    if (responsedata.data.favTrainerNotification) {
      this.isActive = true;
    }
    else {
      this.isActive = false;
    }
   }
  }
  onFailure(type: any, response: string) {
    
  }

}



