import { ActivatedRoute } from '@angular/router';
import { Constants } from 'core/constants';
import { LocalStorageService } from './../../core/service/local-storage.service';
import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { SeoElements } from '../../core/constants';
import { UserLogin } from 'core/models/userloginresponse';
import { FBPixel } from 'fbpixelCustomEvents';
import { GlobalSubscriptionService } from 'app/globalSubcription.service';
import { UserSession } from 'core/sessions';
declare var $: any;

@Component({
  selector: 'app-useroptions',
  templateUrl: './useroptions.component.html',
  styleUrls: ['./useroptions.component.css']
})
export class UseroptionsComponent implements OnInit {
   userType:number;
   sub:any;
   activeTab:any='membership';
   creditAvailable:UserLogin;
   currencyCode:any;
   public isPrime=UserSession.getDynamicElements(Constants.isPrime);
   public isLoggedIn = UserSession.isLoggedIn();

  constructor(private meta : Meta,private route:ActivatedRoute, private globalSubscriptionService: GlobalSubscriptionService,private titleService:Title) {
    this.meta.updateTag({ name: SeoElements.keywords, content: SeoElements.settingsMetaKeywords });
    this.meta.updateTag({name: SeoElements.title , content: SeoElements.settingsMetaTitle})
    this.meta.updateTag({ name: SeoElements.description, content: SeoElements.settingsDescription });
   }

  ngOnInit() {
    this.titleService.setTitle(UserSession.getSeoElements.settingsTitle)

    this.currencyCode = LocalStorageService.getCurrencyCode();
    FBPixel.openMembershipEvent();
    if(this.isLoggedIn&&LocalStorageService.getUser().data.walletDto) {
      this.creditAvailable = LocalStorageService.getUser().data.walletDto.balance;
    }

  this.userType=LocalStorageService.getUserType();

    this.sub = this.route.params.subscribe(params => {
      if (params.hasOwnProperty('sessionId') && params['sessionId'] != null) {
        
        if(params['sessionId']=='transaction'){
          this.activeTab = 'membership'
          setTimeout(() => {
            this.activeTab = 'transaction';
         }, 500);
        }
        else if(params['sessionId']=='notification'){
          this.activeTab = 'membership'
          setTimeout(() => {
            this.activeTab = 'notification';
         }, 500);
        }
        else if(params['sessionId']=='aboutus'){
          this.activeTab = 'membership'
          setTimeout(() => {
            this.activeTab = 'aboutus';
         }, 500);
        }
        else{
          this.activeTab = 'membership'
          FBPixel.openMembershipEvent();

        }
      }
      else{
        this.activeTab = 'membership'
        FBPixel.openMembershipEvent();

      }
    });

    this.globalSubscriptionService.changeCountryState.subscribe((value) => { 
      
      this.currencyCode = LocalStorageService.getCurrencyCode();
      if(this.isLoggedIn&&LocalStorageService.getUser().data.walletDto) {
        this.creditAvailable = LocalStorageService.getUser().data.walletDto.balance;
      }else{
        this.creditAvailable = undefined; 
      }    
    
    });
  

  }

}



