import { Component, OnInit } from '@angular/core';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { Constants } from 'core/constants';
import { LocalStorageService } from 'core/service/local-storage.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { UserLogin } from 'core/models/userloginresponse';
import { Router, ActivatedRouteSnapshot } from '@angular/router';
import { GlobalSubscriptionService } from 'app/globalSubcription.service';
import { ConfirmpopupComponent } from 'classes/confirmationPopup/confirmationPopup.component';
import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { Overlay, overlayConfigFactory } from 'angular2-modal';


@Component({
  selector: 'app-change-country',
  templateUrl: './change-country.component.html',
  styleUrls: ['./change-country.component.css']
})
export class ChangeCountryComponent implements OnInit,HttpSuccesFailureResponse {
  storeData: any;
  currentCountryId:any;
  userId:any;
  
  constructor(private router:Router, private common_http: HttpCommonUtilsService,private http_user_auth: HttpUserAuthUtilsService,private globalSubscription:GlobalSubscriptionService,private modal: Modal) { }

  ngOnInit() {

    this.common_http.getStoreList(this,true); // store list API
    this.currentCountryId=LocalStorageService.getStoreId()
    this.userId=LocalStorageService.getUserId();
  }

  onCountrySelected(store){
    if(store){
      this.currentCountryId=store;
      this.updateStoreID();
    }
   }

   updateStoreID(){
    this.http_user_auth.updateUserStore(this.userId,this.currentCountryId,this,true);
   }

  onSuccess(type: any, responsedata: any) {
    if(type === Constants.FindStoreApi) {
      this.storeData = responsedata.data;
      
    }

    if(type === Constants.UpdateUserStoreApi){ 
      LocalStorageService.setStoreId(responsedata.data.id);
      this.currentCountryId=responsedata.data.id;
      let user:UserLogin =  LocalStorageService.getUser();
          user.data.currencyCode = responsedata.data.currencyCode;
          LocalStorageService.setUser(user)
          this.http_user_auth.getUserProfile(LocalStorageService.getUserId(),this,false);
    }
    if(type == Constants.FindUserApi){
      LocalStorageService.setUser(responsedata);
      this.openconfirmpopup(Constants.countryChangedMessage,Constants.countryChangedTitle);
      //this.globalSubscription.countryChanged(LocalStorageService.getUser().data.currencyCode);
    }

  }
  onFailure(type: any, response: any) {
    if(type === Constants.FindStoreApi) {
    
   }
    if(type === Constants.UpdateUserStoreApi){

    }

  }
  openconfirmpopup(message, title) {
    let parent = this;
    let hideCancelButton = true;
    let yesTitle="Got It"
    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title,parent,hideCancelButton,yesTitle }, BSModalContext));

  }
  confirmAction(){
    this.router.navigate(['/classes']);

  }
  cancelAction(){
    this.router.navigate(['/classes']);
 
  }

}



